import GeoService from '@/services/commons/GeoService'
import DisabledClassMixin from '@/mixins/commons/DisabledClassMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let CountryTableMixin = {
  mixins: [DisabledClassMixin, LoadingMixin, ListLayoutMixin],
  props: {
    defaultCountryTableFilter: {
      type: String,
      default: null
    },
    defaultCountryTableOrderBy: {
      type: String,
      default: null
    },
    disabled: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      countryTableData: [],
      countryTableFilterTimer: null,
      countryTableFilter: this.defaultCountryTableFilter,
      countryTableOrderBy: this.defaultCountryTableOrderBy,
      countryTableContainer: 'loading-container',
      countryTableBusy: false,
      countryTableTotalRows: 0,
      countryTableCurrentPage: 1,
      countryTablePerPage: 10
    }
  },
  computed: {
    countryTableNoDataText: function () {
      return this.countryTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    countryTableTotalPages: function () {
      return Math.ceil(this.countryTableTotalRows / this.countryTablePerPage)
    },
    countryTablePagingInfo: function () {
      return this.pagingInfo(this.countryTableCurrentPage, this.countryTablePerPage, this.countryTableTotalRows)
    }
  },
  methods: {
    loadCountryTableData (loadingContainerId) {
      let self = this
      this.countryTableBusy = true
      this.countryTableContainer = loadingContainerId || this.countryTableContainer
      this.startLoading(this.countryTableContainer)
      GeoService.geos({
        pageIndex: this.countryTableCurrentPage - 1,
        pageSize: this.countryTablePerPage,
        filter: this.countryTableFilter,
        orderByField: this.countryTableOrderBy,
        geoTypeEnumId: 'GEOT_COUNTRY',
        disabled: this.disabled
      }).then(function (response) {
        self.stopLoading(self.countryTableContainer)
        self.countryTableBusy = false
        self.countryTableTotalRows = response.data.totalRows
        self.countryTableData = response.data.geoList || []
      }).catch(function (error) {
        self.stopLoading(self.countryTableContainer)
        self.countryTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshCountryTable () {
      this.loadCountryTableData()
    },
    filterCountryTable (filter) {
      let self = this
      if (this.countryTableFilter !== filter) {
        clearTimeout(this.countryTableFilterTimer)
        this.countryTableFilterTimer = setTimeout(() => {
          self.countryTableFilter = filter
          self.loadCountryTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.countryTableFilterTimer)
      }
    },
    orderCountryTable (orderBy, active) {
      this.countryTableOrderBy = (active ? '' : '-') + orderBy
      this.loadCountryTableData()
    }
  },
  watch: {
    countryTableCurrentPage: function () {
      this.refreshCountryTable()
    },
    countryTablePerPage: function () {
      this.refreshCountryTable()
    }
  }
}

export default CountryTableMixin
